// 
import React, { useState, useEffect, useContext } from 'react';
import style from './SwitchParametres.module.css';
import Switch3 from '../../../../components/UI/CustomSwitch/Switch3/Switch3'
import Switch2 from '../../../../components/UI/CustomSwitch/Switch2/Switch2';
import { StoreContext } from '../../../../context/context';
/////////////////////////////////////////////////////

const SwitchParametres = () => {
	// 
	const { setGameParametres } = useContext(StoreContext); //     
	const [fallsMax, setFallsMax] = useState(parseInt(localStorage.getItem('fallsMax')) || null); //    - 
	const [plus30, setPlus30] = useState(JSON.parse(localStorage.getItem('plus30')) || false); //     plus30
	const [badWords, setBadWords] = useState(JSON.parse(localStorage.getItem('badWords')) || false); //     
	// /////////////////////////////////////////////////////////
	// 

	//  fallsMax plus30 badWords
	useEffect(() => {
		//   gameParametres fallsMax plus30 badWords
		setGameParametres((prevParams) => ({
			...prevParams,
			fallsMax: fallsMax,
			plus30: plus30,
			badWords: badWords,
		}));
		//   localStorage
		localStorage.setItem('fallsMax', fallsMax);
		localStorage.setItem('plus30', plus30);
		localStorage.setItem('badWords', badWords);
		// ///////////////////////////////////////
	}, [fallsMax, plus30, badWords]); //   fallsMax plus30 badWords (    Switch)
	/////////////////////////
	//////////////////////////////////

	//  
	return (
		<>
			<div className='parametr'>
				<div>- :</div>
				<Switch3 value={[4, null, 5]} state={fallsMax} changeState={setFallsMax} />
			</div> {/* -  */}
			<div className='parametr'>
				<div>+30 :</div>
				<Switch2 state={plus30} changeState={setPlus30} />
			</div> {/* +30  */}
			<div className='parametr'>
				<div>  :</div>
				<Switch2 state={badWords} changeState={setBadWords} />
			</div> {/*    */}
		</> //     
	);
	// /////////////////////////////////////
}

export default SwitchParametres;